

/* eslint-disable no-unused-vars */


import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"

import {
  CButton,
  CCard,
  CCardHeader,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'
import Flatpickr from "react-flatpickr"
import { French } from "flatpickr/dist/l10n/fr.js";
import { format } from 'date-fns';
import DatePicker from "react-datepicker";
import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
import $ from 'jquery';
import conf from '../../../conf.js';
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
import ville from "../../dict/ville.js"
import { storage } from "../../../firebase/firebase";
import ModalMessage from "../../pages/messages/ModalMessage"
const animatedComponents = makeAnimated();

// const cities = require('all-the-cities');

registerLocale('es', es)


  const optionGroup = [
    {
      
      options: [
        { label: "PDF", value: "PDF" },
        { label: "MP4", value: "MP4" },
        { label: "JPEG", value: "JPEG" },
        { label: "MP3", value: "MP3" },
        { label: "EXCEL", value: "EXCEL" },
        
      ]
    },
    
  ]
  const optionType = [
    {
      options: [
        { label: "Tournoi Inter-Entreprise", value: conf.type_tournoi.TOURNOI_INTER_ENTREPRISE },
        { label: "Tournoi Inter-Ecole", value: conf.type_tournoi.TOURNOI_INTER_ECOLE },
      ]
    },
  ]
  
class ModifierTournoi extends Component {
  constructor(props) {
    super(props)
    this.state = {
      user:{},
      type:"",
      ok:"",
      annee:"",
      date_debut:"",
      date_fin:"",
      date_debut_inscription:"",
      date_fin_inscription:"",
      date_creation:"",
      equipe:{},
      debut:"",
      date:new Date(),
      fin:"",
      year:"",
      show:false,
      success:true,
      boutton:"OK",
      message:"Tournoi Modifi avec succs !!",
      titre:"Message modification de tournoi",
      files_preview:"",
      files_name:{file:""},
      multimedia:{file:""},
      multiUpdate:"",
      programme:""
    }
    this.handleChangeTypeTournois.bind(this);
    this.handleDateFin.bind(this);
    this.handleDateDebut.bind(this);
    this.handleDateFinInscription.bind(this);
    this.handleDateDebutInscription.bind(this);
    this.handleDate.bind(this);
}

componentDidMount() {
  var array=[]
  var date =new Date()
  for(var i=parseInt(date.getFullYear());i<parseInt(date.getFullYear())+5;i++){
    array.push({ label: i, value: i })
    this.setState({date:array})
  }


  this.setState({equipe:[]})
  let  data=  JSON.parse(localStorage.getItem('token_auth'))
  var id=localStorage.getItem("idTournoi")

  const requestOptions = {
    method: 'GET',
    headers: { 
      'Authorization':'Bearer '+data.token,

    },
  };

  fetch(conf.url+"tournoi/tournois/"+id,requestOptions)
  .then((response) => response.json())
  .then((data) => {
    console.log("data",data);
    this.setState({type:data.typeTournoi})
    this.setState({equipe:data})
    $("#nom").val(data.nom);
    $("#annee").val(data.annee);
    $("#description").val(data.description);
    
    $("#prixparsa").val(data.prixPartSa);
 
    $("#datedebut").val(data.dateDebut);
    $("#datefin").val(data.dateFin);

  
    this.setState({date_debut:data.dateDebut})
    this.setState({date_debut_inscription:data.dateDebutInscription})
    this.setState({date_fin_inscription:data.dateFinInscription})
    this.setState({date_fin:data.dateFin,programme:data?.multimediaProgramme?.file})
    this.setState({date_creation:data.annee,date_debut_creation:data.annee})
     try{
      if(data.multimedia.file!=undefined){
        storage.ref("images")
        .child(data.multimedia.file).getDownloadURL()
        .then((url) => {
          
            this.setState({files_preview:url})
            this.setState({multiUpdate:data.multimedia})
         
        })
       }
     }catch(error){

     }
   


    })



    $("input[data-preview]").change(() =>{
      var data=[]
      console.log("fiel =====>",Array.from(document.getElementById("autreFile").files))
      var date=new Date()
      
      var my_files = Array.from(document.getElementById("autreFile").files)
        var fileNom=date.getFullYear()+""+date.getMonth()+""+date.getDay()+""+date.getSeconds()+""+date.getMilliseconds()+""+my_files[0].name
        this.setState({files_name:{file:my_files[0],fileName:fileNom},multimedia:{file:fileNom}})
        
        var fileReader = new FileReader();
        fileReader.readAsDataURL(my_files[0]);
        fileReader.onload = (fileEvent)=> {
          this.setState({files_preview:fileEvent.target.result})
    
        };

    
    });
}
handleUpdateTournois=()=>{
   
  var my_files = Array.from(document.getElementById("programme").files)
  var programme={file:this.state.programme}
  var fileNom=""
  var media=null
  var date=new Date()

  if(my_files.length){
     fileNom=date.getFullYear()+""+date.getMonth()+""+date.getDay()+""+date.getSeconds()+""+date.getMilliseconds()+""+my_files[0].name
     programme={file:fileNom}
    storage.ref("images/"+fileNom).put(my_files[0]).then(()=>{})
  }


  
  let  token=  JSON.parse(localStorage.getItem('token_auth'))
  var data= {
    "id":this.state.equipe.id,
    "nom": $("#nom").val(),
    "annee": this.state.date_creation,
    "description":$("#description").val(),
    "prixPartSrl":0,//$("#prixparsrl").val(),
    "prixPartSa":$("#prixparsa").val(),
    "prixPartEcole":0,//$("#prixparecole").val(),
    "prixPme":0,//$("#prixpme").val(),
    "dateDebut": this.state.date_debut,
    "dateFin": this.state.date_fin,
    "dateDebutInscription": this.state.date_debut_inscription,
    "dateFinInscription": this.state.date_fin_inscription,
    "typeTournoi":this.state.type,
    "multimedia":this.state.multiUpdate,
    "multimediaProgramme":programme.file


  }

  if(this.state.files_name.file!=""){
    media=this.state.multimedia
    storage.ref("images/"+this.state.files_name.fileName).put(this.state.files_name.file).then(()=>{})
    data= {
      "id":this.state.equipe.id,
      "nom": $("#nom").val(),
      "annee": this.state.date_creation,
      "description":$("#description").val(),
      "prixPartSrl":0,//$("#prixparsrl").val(),
      "prixPartSa":$("#prixparsa").val(),
      "prixPartEcole":0,//$("#prixparecole").val(),
      "prixPme":0,//$("#prixpme").val(),
      "dateDebut": this.state.date_debut,
      "dateFin": this.state.date_fin,
      "dateDebutInscription": this.state.date_debut_inscription,
      "dateFinInscription": this.state.date_fin_inscription,
      "typeTournoi":this.state.type,
      "multimedia":media,
      "multimediaProgramme":programme.file
  
  
    }
  }

  console.log("data a transmettre",data)

  const requestOptions = {
    method: 'POST',
    headers: { 'Content-Type': 'application/json','Accept': 'application/json',
    'Authorization':'Bearer '+token.token,
  },
    body: JSON.stringify(data)
};

fetch(conf.url+"tournoi/update",requestOptions)
.then((response) => response.json())
.then((data) => {
   console.log("okkk====>",data)
   try{
    if(data.id){
     this.setState({show:true,success:true});
    }else{
     this.setState({show:true,success:false});
    }
 }catch(err){
   this.setState({show:true,success:false});
 }

 }
 
);
  
} 

handleChangeTypeTournois = (e) => {
  console.log("adate =====>",e)
  try{
    this.setState({type:e.value})
   
  }catch(ee){

  }

}
handleDateDebut= (date)=> {

  if(date.length!=0){
    
    this.setState({date_debut:date[0]});
  }

  
}

handleDateFin= (date)=> {

  if(date.length!=0){
    
    this.setState({date_fin:date[0]});
  }

  
}


handleDateDebutInscription= (date)=> {
  var aujourdhui=new Date()
  if(date.length!=0){
    const NewDate1= format(date[0],'dd/MM/yyyy');
    this.setState({date_debut_inscription:date[0]});
  }
  if(date.length==0){
    this.setState({date_debut_inscription:this.state.date_debut_inscription});
  }  
}

handleDateFinInscription= (date)=> {
  var aujourdhui=new Date()
  if(date.length!=0){
    const NewDate1= format(date[0],'dd/MM/yyyy');
    this.setState({date_fin_inscription:date[0]});
  }
  if(date.length==0){
    this.setState({date_fin_inscription:this.state.date_fin_inscription});
  }

  
}

handleDate= (e)=> {

    this.setState({date_creation:e.value});

}

handleValidate=()=>{
  if(this.state.success){
    this.setState({ok: <Redirect to='/liste-tournoi'/>})
  }else{
    this.setState({show:false})
  }
  
}

render() {
const { pick } = this.state
const { selectedGroup } = this.state
const { selectedLangue } = this.state
const animatedComponents = makeAnimated();
const type_tournois=[{value:"Tournois Inter-Entreprise",label:"Tournois Inter-Entreprise"},{value:"Tournois Inter-Ecole",label:"Tournois Inter-Ecole"}]

return (
    <>
      <React.Fragment>
        <div className="page-content">
         {this.state.ok}

         <ModalMessage
              show={this.state.show}
              success={this.state.success}
              boutton={this.state.boutton}
              handleValidate={this.handleValidate}
              message={this.state.message}
              titre={this.state.titre}
              onCloseClick={() => this.setState({ show: false })}
              />

        <Container fluid={true} className="justify-content-center">

          <Row className="justify-content-center">
          <Col md="12" lg="12" xl="12">

            {/* <Row ClassName="mb-3">
              <Col md="8">
                  <div className="page-title-box d-flex align-items-center justify-content-between">
                    <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                  </div>
              </Col>
            </Row> */}
 
            

            <Card ClassName="mt-5">
              {/* <CCardHeader className="p-4"> */}
                <h1 className="p-4" >Modifier le tournoi</h1>
              {/* </CCardHeader> */}

              <CardBody>
                <Row> 
                  <Col md="4">
                    <label>Nom</label>
                    <Input
                      className="form-control"
                      type="text"
                      id="nom"
                      placeholder="Nom"
                    />
                  </Col>


                  <Col md="4">
                    <label>Type de tounoi</label>
                    <ReactSelect
                      closeMenuOnSelect={true}
                      components={animatedComponents}
                      onChange={this.handleChangeTypeTournois}
                      options={optionType}
                      placeholder={this.state.equipe.typeTournoi}
                    />
                  </Col>

                  <Col md="4">
                    <label>Anne</label>
                    <ReactSelect
                      closeMenuOnSelect={true}
                      components={animatedComponents}
                      onChange={this.handleDate}
                      options={this.state.date}
                      placeholder={this.state.equipe.annee}
                    />
                  </Col>
                  
                </Row>
              
                <Row className="mt-4">
                  <Col md="12">
                    <label>Description</label>
                    <textarea  className="form-control" id="description" placeholder="Description" />
                  </Col>
                </Row>

                <Row className="mt-4">
                  <Col md="6">
                        <label for="name">Numro de tlphone</label>
                        <input type="tel" name="numero" class="form-control" id="numero"/>
                  </Col>

                  <Col md="6">
                    <label>Prix</label>
                    <Input
                      className="form-control"
                      type="number"
                      id="prixparsa"
                      step="5"
                      min="0"
                      
                    />
                  </Col>
              
                </Row>

                <Row className="mt-4">
                  <Col md="6">
                    <label>Date de dbut</label>
                    <Flatpickr
                      // value={pick}
                      className="form-control d-block"
                      placeholder={this.state.date_debut}
                      options={{
                        
                        //Minimum aujourd'hui
                        minDate: "today",

                        //Desactiver une liste de dates
                        //disable: ["2025-01-30", "2025-02-21", "2025-03-08", new Date(2025, 4, 9) ],

                        //Langue
                        locale:French,
                        defaultDate: ["10/12/2016"],
                        //Cache votre entre originale et en cre une nouvelle.
                        altInput: true,
                        
                        //Format de la date
                        altFormat: "d/M/Y H:i",
                        // dateFormat: "Y-m-d H:i",
                        enableTime: true,
                        time_24hr: true,
                        minuteIncrement:1,
                        defaultHour:0,
                        defaultMinute:0,
                        enableSeconds:true,
                      }}
                       onChange={this.handleDateDebut}
                    />
                    {new Date(this.state.equipe.dateDebut).toLocaleString()}
                  </Col>
                  <Col md="6">
                    <label>Date de fin</label>
                    <Flatpickr
                      // value={pick}
                      className="form-control d-block "
                      placeholder={this.state.date_fin}
                      options={{
                        
                        //Minimum aujourd'hui
                        minDate: "today",

                        //Desactiver une liste de dates
                        disable: ["2025-01-30", "2025-02-21", "2025-03-08", new Date(2025, 4, 9) ],

                        //Langue
                        locale:French,

                        //Cache votre entre originale et en cre une nouvelle.
                        altInput: true,
                        
                        //Format de la date
                        altFormat: "d/M/Y H:i",
                        // dateFormat: "Y-m-d H:i",
                        enableTime: true,
                        time_24hr: true,
                        minuteIncrement:1,
                        defaultHour:23,
                        defaultMinute:59,
                        enableSeconds:true,
                      }}
                      onChange={this.handleDateFin}
                    />
                    {new Date(this.state.equipe.dateFin).toLocaleString()}
                  </Col>
                </Row>
                
                <Row className="mt-4">
                      <Col md="6">
                        <label>Date de dbut d'inscription</label>
                        <Flatpickr
                          // value={pick}
                          className="form-control d-block"
                          placeholder="Date de dbut d'inscription"
                          options={{
                            
                            //Minimum aujourd'hui
                            minDate: "today",

                            //Desactiver une liste de dates
                            // disable: ["2025-01-30", "2025-02-21", "2025-03-08", new Date(2025, 4, 9) ],

                            //Langue
                            locale:French,

                            //Cache votre entre originale et en cre une nouvelle.
                            altInput: true,
                            
                            //Format de la date
                            altFormat: "d/M/Y H:i",
                            // dateFormat: "Y-m-d H:i",
                            enableTime: true,
                            time_24hr: true,
                            minuteIncrement:1,
                            defaultHour:0,
                            defaultMinute:0,
                            enableSeconds:true,
                          }}
                           onChange={this.handleDateDebutInscription}
                        />
                          {new Date(this.state.equipe.dateDebutInscription).toLocaleString()}
                      </Col>
                      <Col md="6">
                        <label>Date de fin d'inscription</label>
                        <Flatpickr
                          // value={pick}
                          className="form-control d-block "
                          placeholder="Date de fin du tournoi"
                          options={{
                            
                            //Minimum aujourd'hui
                            minDate: "today",

                            //Desactiver une liste de dates
                            // disable: ["2025-01-30", "2025-02-21", "2025-03-08", new Date(2025, 4, 9) ],

                            //Langue
                            locale:French,

                            //Cache votre entre originale et en cre une nouvelle.
                            altInput: true,
                            
                            //Format de la date
                            altFormat: "d/M/Y H:i",
                            // dateFormat: "Y-m-d H:i",
                            enableTime: true,
                            time_24hr: true,
                            minuteIncrement:1,
                            defaultHour:23,
                            defaultMinute:59,
                            enableSeconds:true,
                          }}
                          onChange={this.handleDateFinInscription}
                        />
                        {new Date(this.state.equipe.dateFinInscription).toLocaleString()}
                      </Col>
                    </Row>

                    <Row className="mt-4">
                  
                    <Col md="6">
                      <label>Image Logo</label>
                      <Input
                        className="form-control"
                        type="file" 
                        data-preview=".preview"
                        placeholder="Logo"
                        id="autreFile"
                      />
                    </Col>
                    <Col md="6">
                    <img style={{width:"150px",heigth:"150px"}} className="card-img-top" src={this.state.files_preview} alt="Image"/>    
                    </Col>
                  </Row>

                  <Col md="6">
                      <label>Document PDF</label>
                      <Input
                        className="form-control"
                        type="file" 
                        defaultValue={this.state.programme}
                        placeholder="programme matchs"
                        id="programme"
                      />
                   </Col>

                <Row className="justify-content-start mt-5">
                 
                  <Col md="6">
                    <Button  color="secondary" block className="b-lg">
                      Annuler
                    </Button>
                  </Col>
                  <Col md="6">
                    <Button id="initial" onClick={this.handleUpdateTournois} color="success" block className="b-lg" >
                      Modifier le tournoi
                    </Button>
                    {/* <Button id="chargement" color="info" className="b-lg" type="button" disabled>
                    <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                      Envoi...
                    </Button> */}
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
         
  </React.Fragment>
</>
    )}


  }
  export default ModifierTournoi

